<template>
    <div id="topsearch">
        <div id="results">
            <div class="box" v-show="isSearch">
                <ul  class="results">
                    <p v-show="warn" class="warn">{{ warn }}</p>
                    <router-link class="result" 
                    v-for="(result,index) in results" 
                    :key="index" 
                    :to="result.href"
                    @click="jump(result.maodian)"
                    >
                        <li>
                            <span v-for="(i,index) in result"
                            :key="index" 
                            :class="i.ifkey?'key':''">
                                {{i.content}}
                            </span>
                        </li>
                    </router-link>
                    
                </ul>
                <div class="bottom"></div>
            </div>
            <div class="mask" 
            v-show="isSearch" 
            @click="isSearch=0;"
            >
            </div>
        </div>
        <div class="topsearch"
        @mouseenter="isOn=1"
        @mouseleave="isOn=0"
        :class="isAct? 'active' : '' "
        >
            <input type="search"
            @focus="isFocus=1"
            @blur="isFocus=0"
            v-model="content"
            @keydown.enter="Search"
            >
            <div v-show="content" @click="content=''" class="clear">×</div>
            <button @click="Search"></button>
            
        </div>
    </div>
</template>
<script>
import { useStore } from 'vuex'

import axios from 'axios';

// import { useRouter } from 'vue-router';

import {searchKW} from '../hooks/searchKW'
import {matchKW} from '../hooks/matchKW'

import{ref,watch,computed} from 'vue'

    export default {
        name: 'topSearch',
        setup(){
            let isFocus=ref(0)
            let isOn=ref(0)
            let isSearch=ref(0)
            // 判断输入框是否active
            let isAct=computed(()=>{
                return isFocus.value||isOn.value||isSearch.value
            })

            const store=useStore()
            let bmaincolor=store.state.bmaincolor
            let tmaincolor=store.state.tmaincolor

            //搜索时禁止页面滚动
            watch(isSearch,(n)=>{
                if(n){
                    //禁止页面滚动
                    document.body.style.overflow='hidden'
                }else{
                    document.body.style.overflow=''
                }
            })

            //将搜索栏状态传递给vuex
            watch(isAct,()=>{
                store.commit('topsearch/spreadAction',isAct)
            })
            //建立database
            let serverstate=store.state.serverstate
            let brunches=computed(()=>{
                return store.state.brunches.brunches
            })
            let database=ref([])
            let d=ref([])
            let a=()=>{
                axios({
                url:`${serverstate}/all`,
                method:'GET'}).then(res=>d.value=res.data)
            }
            a()
            watch(d,()=>{
                database.value=d.value
            })
            //建立搜索项
            let warn=ref('')
            let content=ref('')
            let results=ref([])
            let Search=()=>{
                isSearch.value=1
                //判断输入内容是否为空或全为空格
                if(content.value&&!content.value.match(/^\s+$/)){
                    warn.value=''
                    let arr=[]
                    searchKW(database.value,content.value,arr)
                    results.value=arr
                    if(results.value.length){
                        results.value.forEach((result,k)=>{
                            let newarr=[]
                            // 提取关键字matchKW(待匹配字符串,关键字,新的数组)
                            // [{content: ,ifkey:false},
                            // {content: ,ifkey:true},
                            // {content: ,ifkey:false}]
                            matchKW(result,content.value,newarr)
                            results.value[k]=newarr

                            let trueres=result.split('—')
                            brunches.value.forEach((i)=>{
                                if (i.name==trueres[0]){
                                    // router.push({path:i.route+'#'+trueres[trueres.length-1]})
                                    results.value[k].href=i.route
                                    //当跳转目的地只是为路由页面时，不进行锚点跳转
                                    if(trueres.length!=1){
                                        //定时器防止锚点跳转失效
                                        results.value[k].maodian=trueres[trueres.length-1]
                                    }
                                }
                            })
                        })
                    }
                    else {
                        warn.value='无结果'
                    }
                }
                else {
                    results.value=[]
                    warn.value='关键词不能为空'
                }
            }
            let timer
            function jump(maodian){
                isSearch.value=0
                clearTimeout(timer)
                timer=setTimeout(()=>{
                    document.getElementById(maodian).scrollIntoView()
                },500)
            }

            return{
                isAct,
                isFocus,
                isOn,
                isSearch,
                bmaincolor,
                tmaincolor,
                content,
                warn,
                results,
                jump,
                Search
            }
        }
    }
</script>
<style lang="less" scoped>
    @b-m-color:v-bind(bmaincolor);
    @t-m-color:v-bind(tmaincolor);
    
    @h:30px;
    @w:10 * @h;
    @b-h:@h*0.9;

    #topsearch{
        margin: 0;
        #results{
            position: relative;
            z-index: 1;
            .box{
                position: absolute;
                z-index: 2;
                top: 0.5 * @h;
                left: 50px;
                box-shadow:  6px 9px 4px rgba(0, 0, 0, .04),
                             0px 8px 20px rgba(0, 0, 0, .08);
                .results{
                    width: @w * 1.25;
                    max-height: 370px;
                    overflow: hidden;
                    overflow-y: scroll;
                    &::-webkit-scrollbar {
                        width: 4px;
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        background: #fff;
                    }
                    &::-webkit-scrollbar-track {
                        border-radius: 0;
                        background: #fff;
                    }
                    &:hover{
                        &::-webkit-scrollbar-thumb {
                            background: rgba(0,0,0,0.2);
                        }
                        &::-webkit-scrollbar-track {
                            background: rgba(0,0,0,0.1);
                        }
                    }
                    background-color: rgb(255, 255, 255);
                    box-shadow: 0 12px 32px 4px rgba(0,0,0,.04),
                                0 8px 20px rgba(0,0,0,.08);
                    border-radius: 4px;
                    padding-top: 40px;
                    .warn{
                        padding-bottom: 25px;
                        color: rgb(110, 110, 110);
                    }
                    .result{
                        display: block;
                        height: 40px;
                        width: 90%;
                        //调正滚动条宽度影响
                        margin-left:18.55px+4;
                        margin-right:18.55px-4;
                        margin-bottom: 5px;
                        padding-left: 5px;
                        border-radius: 4px;
                        box-shadow: 0 1px 3px 0 rgb(212,217,225);
                        text-align: left;
                        line-height: 40px;
                        overflow: hidden;
                        white-space: nowrap; 
                        text-overflow: ellipsis;
                        color: rgb(41, 41, 41);
                        transition: all .2s;
                        cursor: pointer;
                        span{
                            font-size: 15px;
                            transition: all .2s;
                        }
                        .key{
                            color: @b-m-color;
                        }
                        &:hover{
                            background-color: @b-m-color;
                            color: #fff;
                            span{
                                color: #fff;
                            }
                            .key{
                                color: #fff;
                            }
                        }
                    }      
                }
                .bottom{
                    margin-top: 0;
                    width: 100%;
                    height: 50px;
                    background-color: #fff;
                    box-shadow: 0 -1px 0 0 rgb(224,227,232),
                                0 -3px 6px 0 rgba(69,98,155,.12);
                }
            }
            .mask{
                position: fixed;
                left: 50%;
                top: 50%;
                transform: translate(-50%,-50%);
                z-index: 1;
                width: 100vw;
                height: 101vh;
                background: rgba(255, 255, 255, .8);
                
            }
        }
        .topsearch {
            display: flex;
            overflow: hidden;
            position: relative;
            z-index: 2;
            // transform: translateY(50%);
            margin-left: 50px;
            margin-right: 100px;
            padding-right: ((@h - @b-h) / 2);
            height: @h;
            width: @w;
            background-color: rgba(246,247,248,.9);
            border-radius: @h * 0.5;
            border: 3px solid transparent;
            align-items: center;
            transition-property: border,width;
            transition-duration: .3s;
            transform-origin: left;
            input{
                width: 100%;
                background-color: transparent;
                color: rgb(142, 142, 142);
                text-indent: 15px;
                &::-webkit-search-cancel-button{
                    display: none;
                }
            }
            .clear{
                width: 13px;
                min-width: 13px;
                height: 13px;
                min-height: 13px;
                margin-right: 3px;
                border-radius: 50%;
                background: rgb(199,199,199);
                line-height: 12px;
                font-size: 4px;
                color: rgba(246,247,248,.9);
                transition: all .3s;
                opacity: 0;
                cursor: pointer;
                &:hover{
                    background: rgb(91, 91, 91);
                }
            }
            button{
                height: @b-h;
                width: @b-h;
                min-height: @b-h;
                min-width: @b-h;
                background-color: transparent;
                border-radius:50%;
                font-family: 'iconfont';
                color: @b-m-color;
                transition: all .3s;
                cursor: pointer;
                &::after{
                    content: '\e622';
                
                }
                &:hover{
                    background-color: @b-m-color;
                    color: @t-m-color
                }
            }
            
        }
        .active{
            width: @w * 1.25;
            height: @h+6;
            border: 3px solid rgb(0, 128, 255);
            border-radius: (@h + 6) * 0.5;
            background-color: rgb(255, 255, 255);
            // animation: name duration timing-function delay iteration-count direction fill-mode;
            // animation: active 1s ease-in-out 0s infinite alternate forwards;
            .clear{
                opacity: 1;
                animation: rotate .5s ease-out 0s;
            }
            button{
            
                animation: rotate .5s ease-out 0s;
            }

        }
        @keyframes active {
            100%{
                box-shadow: 0 0 5px 1px rgba(84, 149, 246, 0.9);
            }
        }
        @keyframes rotate {
            100%{
                transform: rotate(360deg);
            }
        }
    }
</style>