// 提取关键字matchKW(待匹配字符串,关键字,新的数组)
// [{content: ,ifkey:false},
// {content: ,ifkey:true},
// {content: ,ifkey:false}]
export function matchKW(str, keyword, newstr) {
    let length = keyword.length
        //正则使可匹配大小写
    let kw = new RegExp(keyword, 'i')
    let key = str.match(kw)
    let head = str.slice(0, key.index)
    let body = key[0]
    if (head.length) newstr.push({ content: head, ifkey: false })
    if (body.length) newstr.push({ content: body, ifkey: true })
    let foot = str.slice(key.index + length)
    if (foot.match(keyword)) matchKW(foot, keyword, newstr)
    else newstr.push({ content: foot, ifkey: false })
}

export default matchKW