<template>
    <router-link :to=route
    class="card ban-select"
    active-class="active"
    >
    {{name}}
    </router-link>
</template>
<script>

import { useStore } from 'vuex';

import{ref,reactive,toRefs} from 'vue'

export default {
    name:'navCard',
    props:['brunch'],
    setup(props) {
        let p = props
        let brunch = reactive(p.brunch)

        let isAct=ref(0)


        const store=useStore()
        let bmaincolor=store.state.bmaincolor

        return{
            bmaincolor,
            isAct,
            ...toRefs(brunch),
        }
    }
}
</script>
<style lang="less" scoped>
    @bmaincolor:v-bind(bmaincolor);
    .card{
        display: block;
        width: 202px;
        height: 40px;
        margin-bottom: 5px;
        border-radius: 10px;
        border: 2px solid transparent;
        background-color: rgb(246,247,248);
        color: rgb(95,100,107);
        line-height: 40px;
        transition: all .3s;
        cursor: pointer;
        &:hover{
            color:@bmaincolor;
        }
    }
    .active{
        border: 2px solid @bmaincolor;
        background-color: transparent;
        color:@bmaincolor;
        font-weight: 700;
    }
</style>