//返回y以及鼠标所需进入和离去函数
//y需通过v-bind传递给style
import { ref } from 'vue'
export function getFloat() {
    let y = ref('-50%')
    let come = null

    function Come() {
        let Y = -50
        let k = 0.9 //浮动幅度
        Y *= k
        y.value = Y + '%'
        k = 1 / k
        come = setInterval(() => {
            Y *= k
            y.value = Y + '%'
            k = 1 / k
        }, 550)
    }

    function Go() {
        clearInterval(come)
        return y.value = '-50%'
    }
    return {
        y,
        Come,
        Go
    }
}