<template lang="">
    <div id="box" >
        <div class="mask"
        :class="tss?'pushed':''"
        >
            <router-link class="home" 
            to="/"
            @mouseenter="Come"
            @mouseleave="Go"
            >
            </router-link>
        </div>
    </div>
</template>
<script>
import {computed} from 'vue'
import { useStore } from 'vuex'
import{getFloat}from '../hooks/getFloat'
export default {
    name:'goHome',
    setup(){
        const store=useStore()
        let tmaincolor=store.state.tmaincolor
        let bmaincolor=store.state.bmaincolor

        
        let tss=computed(()=>{
            return store.state.topsearch.isact
        })

        //home浮动hook
        let {y,Come,Go}=getFloat()


        return{
            tmaincolor,
            bmaincolor,
            tss,
            y,
            Come,
            Go
        }
    }
}
</script>
<style lang="less" scoped>
    @t-m-color:v-bind(tmaincolor);
    @b-m-color:v-bind(bmaincolor);

    @w:70px;
    //弧角位移为(√d^2-r^2)-r
    @arc:unit(sqrt(@w * @w * 0.75)-(@w / 2),px);

    @y:v-bind(y);
    // @y:percentage();
    #box{
        position: relative;
        width: @w;
        height: @w;
        min-width: @w;
        min-height: @w;
        margin: 0;
        top: 50%;
        right: 0;
        transition: all .3s;
        //圆弧样式复用
        .arc(@x){
            content:'';
            position: absolute;
            bottom: 50%;
            width: (@w / 2);
            height: (@w / 2);
            background-color: transparent;
            box-shadow: @x ((@w / 2) / 2) white;
        }
        &::before{
            .arc(1px);
            left: -@arc;
            border-bottom-right-radius: 100%;
        }
        &::after{
            .arc(-1px);
            z-index: -1;
            right: -@arc;
            border-bottom-left-radius: 100%;
        }
        
        .mask{
        z-index:0;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: white;
        border-radius: 50%;
        transition: all .5s;
        .home{
            position: absolute;
            top: 50%;
            left: 50%;
            width: 85%;
            height: 85%;
            background-color: transparent;
            border-radius: 50%;
            border: 3px solid @b-m-color;
            transition: all .8s;
            transform: translate(-50%,@y);
            cursor: pointer;
            &:hover{
                background: @b-m-color;
                animation: light 1s ease infinite alternate;
                &::after{
                    color: @t-m-color;
            }
            }
            @l:190;
            @keyframes light {
                100%{
                    box-shadow: 
                    0px 0 .5px hsla(@l, 95%, 80%, 1),
                    0px 0 1px hsla(@l, 91%, 80%, .95),
                    0px 0 2px hsla(@l+1, 91%, 80%, .95),
                    0px 0 3px hsla(@l+1, 91%, 80%, .95),
                    0px 0 4px hsla(@l+1, 91%, 82%, .9),
                    0px 0 5px hsla(@l+2, 91%, 82%, .9),
                    0px 0 10px hsla(@l+3, 91%, 84%, .9),
                    0px 0 20px hsla(@l+4, 91%, 86%, .85),
                    0px 0 40px hsla(@l+5, 91%, 86%, .85),
                    0px 0 60px hsla(@l+5, 91%, 86%, .85);
                }
            }
            &::after{
                    content: "\e7a7";
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    font-family: iconfont;
                    font-size: 24px;
                    color: @b-m-color;
                    transition: all .3s;
                }
        }
        
        
    }
    .pushed{
        transform: rotate(-360deg);
    }
    }
    
</style>