//传入三个参数:数据库,关键词,空数组
//数据库结构[{[...]}],sections即为数组在对象中的属性名
export function searchKW(base, word, r, pre = '') {
    if (word) {
        //正则使可匹配大小写
        word = new RegExp(word, 'i')
        base.forEach((obj, k) => {
            if (obj.title) {
                if (obj.title.match(word)) {
                    if (pre) {
                        r.push(pre + obj.title)
                    } else r.push(obj.title)
                }
                if (obj.sections) {
                    //需要一个临时变量,使pre能够执行'撤回'操作,否则将记录下遍历到目标的所有路径
                    let n = pre
                    pre += obj.title + '—'
                    searchKW(obj.sections, word, r, pre)
                    pre = n
                }
                //判断是否到头:当obj没有sections且没有弟时
                if (!obj.sections && k == base.length - 1) pre = ''
            }
        });
    }
}