<template>
    <div id="nav">
        <navCard
        v-for="(brunch) in brunches"
        :key="brunch.id"
        :brunch="brunch"
        />
    </div>
</template>
<script>
import navCard from './navCard.vue'

import { useStore } from 'vuex';

import{computed,ref,onMounted,onUnmounted} from 'vue'
export default {
    name:'theNav',
    components:{
        navCard
    },
    setup() {
        const store=useStore()
        let bmaincolor=store.state.bmaincolor

        let brunches=computed(()=>{
            return store.state.brunches.brunches
        })
        
        //改变nav position
        let position=ref('absolute')
        let top =ref('75px')
        let Scroll=()=>{
            if(window.pageYOffset>55)
            {
                position.value='fixed'
                top.value='20px'
            }
            else {
                position.value='absolute'
                top.value='75px'
            }
        }
        onMounted(()=>window.addEventListener('scroll',Scroll))
        onUnmounted(()=>window.removeEventListener('scroll',Scroll))


        return{
            bmaincolor,
            brunches,
            position,
            top
        }
    }
}
</script>
<style lang="less" scoped>
    @bmaincolor:v-bind(bmaincolor);
    @position:v-bind(position);
    @top:v-bind(top);
    #nav{
        position: @position;
        z-index: 0;
        top: @top;
        margin-left: 0;
        width: 321px;
        padding-left: 87px;
        //渐变边框
        border-right: 2px solid;
        border-image: -webkit-linear-gradient(transparent , @bmaincolor , transparent)1 10 1; /* 控制边框颜色渐变 */
        border-image: -moz-linear-gradient(transparent , @bmaincolor , transparent)1 10 1;
        border-image: linear-gradient(transparent , @bmaincolor , transparent)1 10 1; /* 标准的必须写在最后 */
    }
</style>