<template lang="">
    <div  class="list ban-select" 
    @mouseenter="isShow=1"
    @mouseleave="isShow=0"
    :class="isShow? 'active' : '' "
    >
        <div :class="drops? 'arrow' : '' ">
            <a :href="href" class="head">
                {{title}}
            </a>
            <ul v-show="drops">
            <a target="_blank"
            v-for="(drop,index) in drops"
            :key="index"
            :href="drop.href"
            >
            <li>{{drop.title}}</li>
        </a>
        </ul>
        </div>
        
    </div>
</template>
<script>
    import {
        reactive,
        ref,
        toRefs,
        computed
    } from 'vue'

    import { useStore } from 'vuex'

    export default {
        name: 'dropDown',
        props: ['list'],
        setup(props) {

            let p = props
            let list = reactive(p.list)

            let isShow = ref(0)

            //compute height of li for style
            let listHeight = computed(() => {
                // 每个li的高度*li的个数
                let itemH = 0
                if(p.list.drops)itemH = 35 * list.drops.length
                return itemH + 'px'
            })

            const store=useStore()
            let bmaincolor=store.state.bmaincolor
            let tmaincolor=store.state.tmaincolor

            return {
                ...toRefs(list),
                isShow,
                listHeight,
                bmaincolor,
                tmaincolor
            }
        }
    }
</script>
<style lang="less" scoped>
    @h: 50px;
    @b-m-color: v-bind(bmaincolor);
    // @b-n-color: rgba(0, 102, 255, 0.5);
    @b-a-color: rgba(98, 161, 255,.7);
    @t-m-color: v-bind(tmaincolor);
    @t-a-color: rgb(0, 67, 168);
    @item-height: 20px;
    // @x:v-bind(listHeight) ;
    // @list-height:@x * @item-height;
    .list {
        width: @h * 2;
        height: @h;
        div {
            position: relative;
            width: 101px;
            height: 100%;
            margin-left: -1px;
            line-height: @h;
            // background-color: @b-n-color;
            cursor: default;
            transition: all .3s;
            .head{
                color: @t-m-color;
                transition: all .3s;
            }
            ul {
            position: absolute;
            overflow: hidden;
            top: 55px;
            height: 0;
            width: 100%;
            background-color: rgb(255, 255, 255);
            box-shadow: 0 12px 32px 4px rgba(0,0,0,.04),
                        0 8px 20px rgba(0,0,0,.08);
            transition: all .3s;
            a {
                display: block;
                height: 35px;
                line-height: 35px;
                transition: all .3s;
                li {
                    font-size: 1vw;
                    transition: all .3s;
                }
                &:hover {
                    background-color: lighten(@b-a-color,15%);
                    transform: scale(1.05);
                    li{
                        color: darken(@b-a-color,20%);
                    }
                }
            }
        }
        }
        .arrow{
                &::after{
                    content: '';
                    position: absolute;
                    top: 35px;
                    right: 50%;
                    // transform: ;
                    height: 6px;
                    width: 6px;
                    border: 1px solid @t-m-color;
                    border-left: 0;
                    border-top: 0;
                    transform:translateX(50%) rotate(45deg);
                    transition: all .3s;
                }
            }
       
    }
    
    .active {
        div {
            background-color: lighten(@b-a-color,10%);
            transform: scaleX(1.03);
            // box-shadow: 0 1px .1px black;
            ul {
                transform:translateY(-5px);
                height: v-bind(listHeight);
                // box-shadow: 0 1px .3px black
            }
        }
        .arrow{
            &::after{
                transform:translate(50%,100%) rotate(225deg);
            }

        }
        
    }
</style>