<template >
    <transition name="gotop">
        <a id="gotop"
        href="javascript:scrollTo(0, 0)"
        @click="goTop"
        v-show="isShow">
        </a>
    </transition>
</template>
<script>
import { useStore } from 'vuex'
import {onMounted,onUnmounted,ref} from 'vue'
export default {
    name:'goTop',
    setup(){
        const store=useStore()
        let goTop=()=>{
            window.scrollTo(0,0)
            // 令display为none
            store.commit('elevator/switch','none')
        }
        let isShow=ref(false)
        let Scroll=()=>{
            if(window.pageYOffset>300)isShow.value=true
            else isShow.value=false
        }
        onMounted(()=>window.addEventListener('scroll',Scroll))
        onUnmounted(()=>window.removeEventListener('scroll',Scroll))

        let bmaincolor=store.state.bmaincolor

        return{goTop,isShow,bmaincolor}
    }
}
</script>
<style lang="less" scoped>
@bmaincolor:v-bind(bmaincolor);
    #gotop{
        position: fixed;
        bottom: 50px;
        right: 20vw;
        width: 50px;
        height: 50px;
        border-radius: 10%;
        box-shadow: 0 1px 10px 0 rgb(212,217,225);
        background-color: transparent;
        backdrop-filter: blur(3px);
        line-height: 50px;
        color: rgb(79, 79, 79);
        transition: all .5s;
        cursor: pointer;
        &::after{
            content: '\e600';
            display: block;
            font-family: iconfont;
            transform: rotate(180deg);
        }
        &:hover{
            background: @bmaincolor;
            color: #fff;
        }
    }
    .gotop-enter-from,
    .gotop-leave-to {
        transform: translateX(70px);
        opacity: 0;
        background: transparent;
    }

    .gotop-enter-to, 
    .gotop-leave-from {
        transform: translateX(0);
    }


</style>